import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 1, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			id:0,
			salesOrder:'',
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
			keyword:'',
			merDetail:[],
			type:0,
			
		};
	},
	
	onLoad(options){
		
		console.log('页面',options);
		this.id = options.id
		this.calcSize();
		getApp().user_api(true);
		
		this.filterIndex = 1;
		this.page = 1;
		// self.type = 0
		this.goodsList = []
		this.getProductList();
		
		
		this.merchantDetail();
	},
	onShow(){
		
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	
	methods: {
		
		merchantDetail(){
			let self = this;
			let path = '/gzh/merchant_api/merchant_detail'
			let data = {id:self.id};
			getApp().http(path, data, function(res) {
				self.merDetail = res;
			});
		},
		getProductList(salesOrder,priceOrder){
			let self = this;
			let path = '/gzh/merchant_api/merchant_product'
			let id1 = self.id 
			let salesOrder1 = salesOrder ? salesOrder : ''
			let priceOrder1 = priceOrder ? priceOrder : ''
			let type1 = self.type ? self.type : 2
			console.log('排序',salesOrder1);
			
			let data = {
				id:id1,
				sales_order:salesOrder1,
				price_order:priceOrder1,
				type:type1,
				page:self.page,
				limit:self.limit
			};
			getApp().http(path, data, function(res) {
				console.log('详情',res);
				uni.hideLoading();
				
				let list = res;
				if (list.length > 0) {
					console.log('总条数',list.length)
					
					
					self.goodsList = self.goodsList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
					
				} else {
					self.status = "";
				}
			})
		},
		//筛选点击
		tabClick(index){
			let self = this;
			console.log('点击',index)
			
			self.filterIndex = index;
			
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			
			uni.showLoading({
				title: '正在加载'
			})
			
			if(self.filterIndex == 0){
				console.log('综合排序')
				self.page = 1;
				self.type = 0
				self.goodsList = []
				self.getProductList()
			}else if(self.filterIndex == 1){
				self.page = 1;
				self.type = 2
				self.goodsList = []
				self.getProductList('desc')
				console.log('销量')
			}else if(self.filterIndex == 2){
				console.log('价格',this.priceOrder)
				self.type = 1
				if(this.priceOrder == 1){
					self.page = 1;
					self.goodsList = []
					self.getProductList('','desc')
				}else{
					self.page = 1;
					self.goodsList = []
					self.getProductList('','asc')
				}
			}
		},
		
		
		clickSell(i){
			console.log(i,'水电费第三方')
			uni.navigateTo({
				url: i
			})
		},
		
		indexUrl(icon){
			
			let http = icon.indexOf('http')
			// console.log('图谱爱',http)
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		
